<template>
  <div class="item">
    <div class="magazine">
      <div class="file">
        <img :src="src" :name="name" @click="del" />
        <p class="name">{{name}}</p>
        <button @click="deleteProject" class="dele" v-show="show">x</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      findName: "",
      show: false
    };
  },
  props: {
    src: String,
    name: String
  },
  methods: {
    del(e) {
      this.findName = e.target.name;
      this.show = !this.show;
    },
    deleteProject() {
      this.$store.commit("deleteProject", this.findName);
      this.success();
      this.show = false;
    },
    success() {
      const h = this.$createElement;
      this.$notify({
        title: "",
        message: h("i", { style: "color: teal" }, "删除成功"),
        position: 'bottom-right'
      });
    }
  }
};
</script>

<style lang="scss" scope>
.item {
  .magazine {
    margin: 20px;
    width: 200px;
    position: relative;
    .name {
      margin-top: 10px;
      text-align: center;
      text-decoration: underline;
      font-weight: bold;
      font-size: 15px;
    }
  }
}

.del {
  margin: 10px 0;
}
.dele {
  position: absolute;
  font-size: 16px;
  line-height: 16px;
  right: -7px;
  top: -7px;
  width: 20px;
  height: 20px;
  padding: 0;
  border-radius: 100%;
  color: #fff;
  border-color: transparent;
  background-color: rgba(15, 15, 15, 0.527);
  font-size: 20px;
  font-weight: bolder;
  cursor: pointer;
  &:hover {
    color: #fff;

    background-color: rgba(0, 0, 0, 0.712);
    border-color: transparent;
    font-size: 20px;
  }
  transition: all 0.5s;
}
.file {
  img {
    cursor: pointer;
  }
}
</style>